.App{
  header{
    width: 100%;
    height: 80px;
    display: flex;
    gap: 162px;
    color: white;
    background-color: #000000;
    align-items: center;
    justify-content: space-around;
  nav{
    width: 364px;
    height: 33px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    list-style-type: none;
  }
    div{
      position: relative;
      img{
        width: 15px;
        position: absolute;
        left: 10px;
        top: 10px;
        background-color: white;
      }
    input{
      padding-left: 30px;
      width: 245px;
      height: 35px;
      font-size: 16px;
      border: 1px solid black;
      border-radius: 40px;
      
    }
    
  }
  aside{
    display: flex;
    gap: 9px;
    
  }
  }
  .promotion{
    width: 100%;
    height: 593px;
    background-color: #EECCD7;
    display: flex;
    justify-content: center;
    align-items: center;
    article{
      margin-left: 120px;
      width: 506px;
      height: 408px;
      padding: 5px 0px;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      gap:34px;
      main{
        width: 506px;
        height: 208px;
        display: flex;
        flex-direction: column;
        gap: 13px;
        h1{
          font-size: 48px;
          font-weight: 700;
          color: black;
        }
        p{
          margin-top: -10px;
          font-size: 24px;
          color: #727272;
        }
        span{
          margin-top: 10px;
          font-size: 36px;
          font-weight: 500;
          color: black;
        }
      }
    }
      button{
        width: 395px;
        height: 88px;
        font-size: 24px;
        font-weight: 500;
        color: white;
        background: linear-gradient(180deg, #EC368D 0%, #D72828 100%);
        border: none;
        border-radius: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 3;
      }
      div{
        position: relative;
        &::before{
          content: '';
          position: absolute;
          right: 40px;
          top: 60px;
          width: 487.54px;
          height: 487.54px;
          border-radius: 100%;
          background: linear-gradient(180deg, rgba(227, 48, 48, 0.28) 0%, rgba(247, 112, 150, 0.0924) 100%);
        z-index: 1;

        }
        &::after{
          content: '';
        position: absolute;
        width: 485.29px;
        height: 485.29px;
        border-radius: 100%;
        right: 60px;
        bottom: 20px;
        background: linear-gradient(180deg, rgba(227, 48, 48, 0.28) 0%, rgba(247, 112, 150, 0.0924) 100%);
        transform: rotate(-16.16deg);
        z-index: 2;

        }
      }
  }
}
